class FormStore {

    constructor() {
        this.store = {}
    }
    
    addStore(name,data) {
        this.store[name] = data
        console.log('输出数据源',this.store)
        data.subscribe && data.subscribe()
    }

    releaseStore (name){
        this.store[name] = {}
    }
}


export default new FormStore